<template>
    <div class="tued-block_editable-image">
        <img :src="imageLink" @load="$emit('updated')" />
        <button type="button" @click="extension.execute('chose-image')"></button>
    </div>
</template>

<script setup>
const { defineProps, defineEmits, computed, inject, onMounted, onUnmounted } = require('vue');
const props = defineProps({
    modelValue: {
        required: true,
        type: Object,
        validator (value) {
            return value.hasOwnProperty('id') && value.hasOwnProperty('filename');
        }
    }
});
const options = inject('options');
const filemanager = inject('filemanager');
const extension = inject('extension.instance').editor('Image');
const emit = defineEmits(['update:modelValue', 'updated']);

const imageLink = computed(() => {
    if (props.modelValue.id) {
        return filemanager.generatePreviewImagePath(props.modelValue);
    }

    return 'data:image/png;base64,';
});

extension.operation('image-chosen', (data, success, fail) => {
    emit('update:modelValue', {
        id: data.id,
        filename: data.filename,
    });

    success();
});

onUnmounted(() => {
    extension.unmount();
});
</script>

<script>
export default {
    name: 'ImageEditor'
}
</script>
